module.exports = "import { Component, OnInit } from '@angular/core';\r\nimport { NzFormatEmitEvent, NzTreeNode } from 'ng-zorro-antd';\r\n\r\n@Component({\r\n  selector: 'nz-demo-tree-draggable',\r\n  template: `\r\n    <nz-tree [(ngModel)]=\"nodes\"\r\n             (nzExpandChange)=\"mouseAction('expand',$event)\"\r\n             [nzDraggable]=\"true\"\r\n             (nzOnDragStart)=\"mouseAction('dragstart',$event)\"\r\n             (nzOnDragEnter)=\"mouseAction('enter',$event)\"\r\n             (nzOnDragLeave)=\"mouseAction('leave', $event)\"\r\n             (nzOnDrop)=\"mouseAction('drop', $event)\"\r\n             (nzOnDragEnd)=\"mouseAction('end', $event)\">\r\n    </nz-tree>`,\r\n  styles  : [ `\r\n    :host ::ng-deep .ant-tree li .ant-tree-node-content-wrapper.ant-tree-node-selected {\r\n      width: calc(100% - 8px);\r\n    }\r\n\r\n    :host ::ng-deep .ant-tree li span[draggable], :host ::ng-deep .ant-tree li span[draggable=\"true\"] {\r\n      width: calc(100% - 8px);\r\n    }\r\n  ` ]\r\n})\r\nexport class NzDemoTreeDraggableComponent implements OnInit {\r\n  nodes = [\r\n    new NzTreeNode({\r\n      title   : 'root1',\r\n      key     : '1001',\r\n      children: [\r\n        {\r\n          title   : 'child1',\r\n          key     : '10001',\r\n          children: [\r\n            {\r\n              title : 'child1.1',\r\n              key   : '100011',\r\n              isLeaf: true\r\n            },\r\n            {\r\n              title   : 'child1.2',\r\n              key     : '100012',\r\n              children: [\r\n                {\r\n                  title   : 'grandchild1.2.1',\r\n                  key     : '1000121',\r\n                  isLeaf  : true,\r\n                  checked : true,\r\n                  disabled: true\r\n                },\r\n                {\r\n                  title : 'grandchild1.2.2',\r\n                  key   : '1000122',\r\n                  isLeaf: true\r\n                }\r\n              ]\r\n            }\r\n          ]\r\n        }\r\n      ]\r\n    }),\r\n    new NzTreeNode({\r\n      title   : 'root2',\r\n      key     : '1002',\r\n      children: [\r\n        {\r\n          title : 'child2.1',\r\n          key   : '10021',\r\n          isLeaf: true\r\n        },\r\n        {\r\n          title   : 'child2.2',\r\n          key     : '10022',\r\n          children: [\r\n            {\r\n              title : 'grandchild2.2.1',\r\n              key   : '100221',\r\n              isLeaf: true\r\n            }\r\n          ]\r\n        }\r\n      ]\r\n    })\r\n  ];\r\n\r\n  mouseAction(name: string, e: NzFormatEmitEvent): void {\r\n    if (name !== 'over') {\r\n      console.log(name, e);\r\n    }\r\n  }\r\n\r\n  constructor() {\r\n  }\r\n\r\n  ngOnInit(): void {\r\n  }\r\n}\r\n"