module.exports = "import { Component } from '@angular/core';\r\nimport { NzTreeNode } from 'ng-zorro-antd';\r\n\r\n@Component({\r\n  selector: 'nz-demo-tree-line',\r\n  template: `\r\n    <nz-tree [(ngModel)]=\"nodes\"\r\n             [nzShowLine]=\"true\"\r\n             [nzDefaultExpandedKeys]=\"expandKeys\"\r\n             (nzExpandChange)=\"mouseAction('expand',$event)\"\r\n             (nzDblClick)=\"mouseAction('dblclick',$event)\"\r\n             (nzContextMenu)=\"mouseAction('contextmenu', $event)\"\r\n             (nzClick)=\"mouseAction('click',$event)\">\r\n    </nz-tree>`\r\n})\r\nexport class NzDemoTreeLineComponent {\r\n  expandKeys = [ '1001', '10001' ];\r\n  nodes = [\r\n    new NzTreeNode({\r\n      title   : 'root1',\r\n      key     : '1001',\r\n      children: [\r\n        {\r\n          title   : 'child1',\r\n          key     : '10001',\r\n          children: [\r\n            {\r\n              title   : 'child1.2',\r\n              key     : '100012',\r\n              children: [\r\n                {\r\n                  title   : 'grandchild1.2.1',\r\n                  key     : '1000121',\r\n                  isLeaf  : true,\r\n                  disabled: true\r\n                },\r\n                {\r\n                  title : 'grandchild1.2.2',\r\n                  key   : '1000122',\r\n                  isLeaf: true\r\n                }\r\n              ]\r\n            }\r\n          ]\r\n        },\r\n        {\r\n          title   : 'child2',\r\n          key     : '10002',\r\n          children: [\r\n            {\r\n              title : 'grandchild2.1',\r\n              key   : '1000122',\r\n              isLeaf: true\r\n            },\r\n            {\r\n              title : 'grandchild2.2',\r\n              key   : '1000123',\r\n              isLeaf: true\r\n            }\r\n          ]\r\n        }\r\n      ]\r\n    }),\r\n    new NzTreeNode({\r\n      title   : 'root2',\r\n      key     : '1002',\r\n      children: [\r\n        {\r\n          title          : 'child2.1',\r\n          key            : '10021',\r\n          children       : [],\r\n          disableCheckbox: true\r\n        },\r\n        {\r\n          title   : 'child2.2',\r\n          key     : '10022',\r\n          children: [\r\n            {\r\n              title : 'grandchild2.2.1',\r\n              key   : '100221',\r\n              isLeaf: true\r\n            }\r\n          ]\r\n        }\r\n      ]\r\n    })\r\n  ];\r\n\r\n  mouseAction(name: string, e: any): void {\r\n    console.log(name, e);\r\n  }\r\n}\r\n"