module.exports = "import { Component, OnInit } from '@angular/core';\r\nimport { NzFormatEmitEvent, NzTreeNode } from 'ng-zorro-antd';\r\n\r\n@Component({\r\n  selector: 'nz-demo-tree-basic',\r\n  template: `\r\n    <nz-tree\r\n      [(ngModel)]=\"nodes\"\r\n      [nzCheckable]=\"true\"\r\n      [nzMultiple]=\"true\"\r\n      [nzDefaultExpandedKeys]=\"expandKeys\"\r\n      [nzDefaultCheckedKeys]=\"checkedKeys\"\r\n      [nzDefaultSelectedKeys]=\"selectedKeys\"\r\n      [nzDefaultExpandAll]=\"expandDefault\"\r\n      (nzClick)=\"mouseAction('click',$event)\"\r\n      (nzDblClick)=\"mouseAction('dblclick', $event)\"\r\n    >\r\n    </nz-tree>`\r\n})\r\n\r\nexport class NzDemoTreeBasicComponent implements OnInit {\r\n  expandKeys = [ '1001', '10001' ];\r\n  checkedKeys = [ '100011', '1002' ];\r\n  selectedKeys = [ '10001', '100011' ];\r\n  expandDefault = false;\r\n  nodes = [\r\n    new NzTreeNode({\r\n      title   : 'root1',\r\n      key     : '1001',\r\n      children: [\r\n        {\r\n          title   : 'child1',\r\n          key     : '10001',\r\n          children: [\r\n            {\r\n              title : 'child1.1',\r\n              key   : '100011',\r\n              isLeaf: true\r\n            },\r\n            {\r\n              title : 'child1.2',\r\n              key   : '100012',\r\n              isLeaf: true\r\n            }\r\n          ]\r\n        },\r\n        {\r\n          title   : 'child2',\r\n          key     : '10002',\r\n          children: [\r\n            {\r\n              title   : 'grandchild1.2.1',\r\n              key     : '1000121',\r\n              isLeaf  : true,\r\n              disabled: true\r\n            },\r\n            {\r\n              title : 'grandchild1.2.2',\r\n              key   : '1000122',\r\n              isLeaf: true\r\n            }\r\n          ]\r\n        }\r\n      ]\r\n    })\r\n  ];\r\n\r\n  mouseAction(name: string, event: NzFormatEmitEvent): void {\r\n    console.log(name, event);\r\n  }\r\n\r\n  ngOnInit(): void {\r\n  }\r\n}\r\n"