module.exports = "import { Component } from '@angular/core';\r\nimport { NzFormatEmitEvent, NzTreeNode } from 'ng-zorro-antd';\r\n\r\n@Component({\r\n  selector: 'nz-demo-tree-dynamic',\r\n  template: `\r\n    <nz-tree [(ngModel)]=\"nodes\"\r\n             [nzAsyncData]=\"true\"\r\n             (nzExpandChange)=\"mouseAction('expand',$event)\">\r\n    </nz-tree>`\r\n})\r\nexport class NzDemoTreeDynamicComponent {\r\n  nodes = [\r\n    new NzTreeNode({\r\n      title   : 'root1',\r\n      key     : '1001',\r\n      children: []\r\n    }),\r\n    new NzTreeNode({\r\n      title   : 'root2',\r\n      key     : '1002',\r\n      children: []\r\n    }),\r\n    new NzTreeNode({\r\n      title: 'root3',\r\n      key  : '1003'\r\n    })\r\n  ];\r\n\r\n  mouseAction(name: string, e: NzFormatEmitEvent): void {\r\n    if (name === 'expand') {\r\n      setTimeout(_ => {\r\n        if (e.node.getChildren().length === 0 && e.node.isExpanded) {\r\n          e.node.addChildren([\r\n            {\r\n              title: 'childAdd-1',\r\n              key  : '10031-' + (new Date()).getTime()\r\n            },\r\n            {\r\n              title : 'childAdd-2',\r\n              key   : '10032-' + (new Date()).getTime(),\r\n              isLeaf: true\r\n            } ]);\r\n        }\r\n      }, 1000);\r\n    }\r\n  }\r\n}\r\n"