module.exports = "import { Component, OnInit } from '@angular/core';\r\nimport { NzTreeNode } from 'ng-zorro-antd';\r\n\r\n@Component({\r\n  selector: 'nz-demo-tree-customized-icon',\r\n  template: `\r\n    <nz-tree [(ngModel)]=\"nodes\"\r\n             [nzShowExpand]=\"false\"\r\n             (nzClick)=\"mouseAction('click', $event)\"\r\n             (nzDblClick)=\"mouseAction('dblclick',$event)\"\r\n    >\r\n      <ng-template #nzTreeTemplate let-node>\r\n        <span class=\"custom-node\" [class.active]=\"node.isSelected\">\r\n          <span>\r\n            <i class=\"anticon anticon-smile-o\" *ngIf=\"node.isExpanded\"></i>\r\n            <i class=\"anticon anticon-frown-o\" *ngIf=\"!node.isExpanded\"></i> {{node.title}}\r\n          </span>\r\n        </span>\r\n      </ng-template>\r\n    </nz-tree>`,\r\n  styles  : [ `\r\n    :host ::ng-deep .ant-tree {\r\n      overflow: hidden;\r\n      margin: 0 -24px;\r\n      padding: 0 24px;\r\n    }\r\n    \r\n    .custom-node {\r\n      cursor: pointer;\r\n      line-height: 30px;\r\n      display: inline-block;\r\n      margin: 0 -1000px;\r\n      padding: 0 1000px;\r\n    }\r\n\r\n    .active {\r\n      background: #1890ff;\r\n      color: #fff;\r\n    }\r\n\r\n    .anticon {\r\n      padding: 0 4px;\r\n    }\r\n  ` ]\r\n})\r\nexport class NzDemoTreeCustomizedIconComponent implements OnInit {\r\n  nodes = [\r\n    new NzTreeNode({\r\n      title   : 'root1',\r\n      key     : '1001',\r\n      expanded: true,\r\n      children: [\r\n        {\r\n          title   : 'child1',\r\n          key     : '10001',\r\n          children: [\r\n            {\r\n              title   : 'child1.1',\r\n              key     : '100011',\r\n              selected: true,\r\n              children: []\r\n            },\r\n            {\r\n              title   : 'child1.2',\r\n              key     : '100012',\r\n              children: [\r\n                {\r\n                  title   : 'grandchild1.2.1',\r\n                  key     : '1000121',\r\n                  isLeaf  : true,\r\n                  checked : true,\r\n                  disabled: true\r\n                },\r\n                {\r\n                  title : 'grandchild1.2.2',\r\n                  key   : '1000122',\r\n                  isLeaf: true\r\n                }\r\n              ]\r\n            }\r\n          ]\r\n        }\r\n      ]\r\n    })\r\n  ];\r\n\r\n  mouseAction(name: string, e: any): void {\r\n    switch (name) {\r\n      case 'click':\r\n        break;\r\n      case 'dblclick':\r\n        setTimeout(() => {\r\n          e.node.isExpanded = !e.node.isExpanded;\r\n        });\r\n        break;\r\n    }\r\n  }\r\n\r\n  ngOnInit(): void {\r\n\r\n  }\r\n}\r\n"