module.exports = "import { Component } from '@angular/core';\r\nimport { NzTreeNode } from 'ng-zorro-antd';\r\n\r\n@Component({\r\n  selector: 'nz-demo-tree-search',\r\n  template: `\r\n    <nz-input-group [nzSuffix]=\"suffixIcon\">\r\n      <input type=\"text\" nz-input placeholder=\"Search Tree Node\" [(ngModel)]=\"searchValue\">\r\n    </nz-input-group>\r\n    <ng-template #suffixIcon>\r\n      <i class=\"anticon anticon-search\"></i>\r\n    </ng-template>\r\n    <nz-tree [(ngModel)]=\"nodes\"\r\n             [nzSearchValue]=\"searchValue\"\r\n             [nzCheckable]=\"true\"\r\n             (nzOnSearchNode)=\"mouseAction('search',$event)\"\r\n             (nzCheckBoxChange)=\"mouseAction('check',$event)\"\r\n             (nzClick)=\"mouseAction('click', $event)\"\r\n             (nzExpandChange)=\"mouseAction('expand',$event)\"\r\n             (nzDblClick)=\"mouseAction('dblclick',$event)\">\r\n    </nz-tree>`\r\n})\r\nexport class NzDemoTreeSearchComponent {\r\n  searchValue;\r\n  nodes = [\r\n    new NzTreeNode({\r\n      title   : 'root1',\r\n      key     : '1001',\r\n      children: [\r\n        {\r\n          title   : 'child1',\r\n          key     : '10001',\r\n          children: [\r\n            {\r\n              title   : 'child1.1',\r\n              key     : '100011',\r\n              children: []\r\n            },\r\n            {\r\n              title   : 'child1.2',\r\n              key     : '100012',\r\n              checked : true,\r\n              children: [\r\n                {\r\n                  title : 'grandchild1.2.1',\r\n                  key   : '1000121',\r\n                  isLeaf: true\r\n                }\r\n              ]\r\n            }\r\n          ]\r\n        },\r\n        {\r\n          title : 'child2',\r\n          key   : '10002',\r\n          isLeaf: true\r\n        }\r\n      ]\r\n    }),\r\n    new NzTreeNode({\r\n      title   : 'root2',\r\n      key     : '1002',\r\n      children: [\r\n        {\r\n          title   : 'child2.1',\r\n          key     : '10021',\r\n          children: []\r\n        },\r\n        {\r\n          title     : 'child1.2',\r\n          key       : '10022',\r\n          selectable: false,\r\n          children  : [\r\n            {\r\n              title : 'grandchild2.2.1',\r\n              key   : '100221',\r\n              isLeaf: true\r\n            }\r\n          ]\r\n        }\r\n      ]\r\n    })\r\n  ];\r\n\r\n  mouseAction(name: string, e: any): void {\r\n    console.log(name, e);\r\n  }\r\n}\r\n"